
.navbar-main-container{
  display: flex;
  position: fixed;
  top: 0;
  width: 100%;
  /* padding: 10px; */
  background-color: #E1E1E1 !important;
  align-items: center;
  justify-content: space-between;
}
.navbar-image-container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.navbar-image{
    height: 80px;
    width: 80;
    background-color: transparent;
}
.navbar-links{
    margin-right: 20px;
}
.navbar-links a{
    padding: 10px;
    text-decoration: none;
}

.navbar-breadMenu-btn{
    cursor: pointer;
    display: none;
}
@media(max-width:1000px){
    .navbar-links{
        
            display: none; /* Hide the links on smaller screens */
            flex-direction: column;
            position: absolute;
            top: 60px; /* Adjust as needed to avoid overlapping with the navbar */
            left: 0;
            width: 100%;
            background-color: #333;
          }

          .navbar-links.open {
            display: flex;
          }
        
          .navbar-breadMenu-btn {
            display:block; /* Show the menu button on smaller screens */
            padding: 10px;
          }
    }
